import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../validate.service'
import {AuthService} from '../auth.service'
import {Router} from '@angular/router';
import  {FlashMessagesService} from 'angular2-flash-messages';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
	name: String;
	username: String;
	password: String;
	email : String;

  constructor(private ValidateService : ValidateService, 
  private flashMessage : FlashMessagesService,
  private authService : AuthService,
  private router:Router) { }

  ngOnInit() {
  }

   onRegisterSubmit()
   {
const user = {
	name: this.name,
	email: this.email,
	username: this.username,
	password: this.password,
   }

   //REquied Fields

   if(!this.ValidateService.validateRegister(user)){
   this.flashMessage.show('Please out all the fields',{cssClass: 'alert-danger', timeout:3000}); return false;
   }


   //REquied Email

   if(!this.ValidateService.validateEmail(user.email)){
   this.flashMessage.show('Please Enter the valid Email',{cssClass: 'alert-danger', timeout:3000});
   return false;
   }


   //REquied PAssword

   if(!this.ValidateService.validatePassword(user.password)){
   this.flashMessage.show('Password should contain 7 to 15 characters which contain at least one numeric digit and a special character',{cssClass: 'alert-danger', timeout:3000});
   return false;
   }

//register USer

// Register user
    this.authService.registerUser(user).subscribe(data => {
      if(data.success){
        this.flashMessage.show('You are now registered and can log in', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/login']);
      } else {
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/register']);
      }
    });
}

}



